body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: Dirooz;
  src: url(fonts/Dirooz.woff) format('woff'), url(fonts/Dirooz.woff2) format('woff2');
  font-display: swap;
}
@font-face {
  font-family: Dirooz-FD;
  src: url(fonts/Dirooz-FD.woff);
}
@font-face {
  font-family: Parastoo-Bold-Fd;
  src: url(fonts/Parastoo-Bold-FD.woff);
}
@font-face {
  font-family: Parastoo-Bold;
  src: url(fonts/Parastoo-Bold.woff);
  font-display: swap;
}
@font-face {
  font-family: Parastoo-FD;
  src: url(fonts/Parastoo-FD.woff);
}
@font-face {
  font-family: Parastoo;
  src: url(fonts/Parastoo.woff);
}
@font-face {
  font-family: Sahel-Black-FD;
  src: url(fonts/Sahel-Black-FD.woff);
}
@font-face {
  font-family: Sahel-Black;
  src: url(fonts/Sahel-Black.woff);
}
@font-face {
  font-family: Sahel-Bold-FD;
  src: url(fonts/Sahel-Bold-FD.woff);
}
@font-face {
  font-family: Sahel-Bold;
  src: url(fonts/Sahel-Bold.woff);
}
@font-face {
  font-family: Sahel-FD;
  src: url(fonts/Sahel-FD.woff);
}
@font-face {
  font-family: Sahel;
  src: url(fonts/Sahel.woff);
}
@font-face {
  font-family: Gandom;
  src: url(fonts/B\ Ziba_0.ttf);
}


.scrollShow {
  animation: shower 1000ms 1 linear;
}

@keyframes shower {
  from {
    opacity: 0.2;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.obsrv {
  transition: opacity 400ms ease-out;
}
.obsrvIn {
  opacity: 1;
}
.obsrvOut {
  opacity: 0;
}
