/* Global Styles */
.thisclass {
  width: 100%;
  font-family: Dirooz; /* Use a common font */
}

/* Container Styles */
.contain {
  border: 1px solid black;
  width: 80%;
  margin: 50px auto;
  /* aspect-ratio: 3/2; */ /* Consider using a wrapper for aspect ratio */
  position: relative;
}

/* Form Container Styles */
.formContain {
  width: 80%;
  height: 100%;
  position: absolute;
  right: 0;
  z-index: 100;
  /* Gradient Background */
  background: linear-gradient(
    135deg,
    #210b4b,
    #3c299c,
    #00779ee7,
    /* Darker blue */ #0093b4c5,
    #00779ee7,
    #00455c
  );
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  /* Add transition for smoother hover effect */
  transition: background-color 0.3s ease;
}

/* Child Element Styles */
.children {
  aspect-ratio: 10/1;
}

/* Select Dropdown Styles */
.thisclass select {
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
  width: 14vw;
  /* Add hover effect */
  transition: box-shadow 0.5s ease;
}

.thisclass select:hover {
  box-shadow: 0px 0px 5px 1px #00000081;
  cursor: pointer;
}

.thisclass select:focus {
  background-color: #504f4f63;
}

.thisclass select:not(:focus) {
  background-color: white;
}
/* ________________________________________ */

/* Custom Styles for Select Container */
.selectContain {
  height: 104%;
  width: 23%;
  position: absolute;
  left: -1%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  top: -2%;
  z-index: 200;
  background: linear-gradient(
    0deg,
    #203566,
    #005d75,
    #7bbbd3,
    #005d75,
    #203566,
    #203566
  );
  box-shadow: 0px 0px 14px 2px black;
}

/* Container Styles */
.contain {
  width: 85%;
  aspect-ratio: 3/2;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

/* Child Element Styles */
.children {
  width: 35%;
  display: inline;
  aspect-ratio: 6/1;
  font-size: 2vw;
  margin: 3% 1%;
  border-radius: 10px;
}

/* Textarea Styles */
.children[type="textarea"] {
  aspect-ratio: 6/7;
  position: absolute;
  left: 20%;
  top: 30%;
}

/* Submit Button Styles */
.formContain input[type="submit"] {
  position: absolute;
  bottom: 40%;
  left: 62%;
  width: 120px;
  color: #f0f0f0;
  aspect-ratio: 11/3;
  font-size: 1.8vw;
  font-family: -apple-system , Sahel-Bold;
  background: linear-gradient(
    90deg,
    #001402,
    #002311,
    #002311,
    #002311,
    #002311,
    #001402,
    #084626,
    #c9dad1,
    #155836,
    #013220
  );
  background: linear-gradient(
    90deg,
    #001402ea,
    #002311e8,
    #002311e8,
    #002311e8,
    #002311e8,
    #002311e8,
    #002311e8,
    #002311e8,
    #001402ea,
    #042d14e1,
    #084626d8,
    #c9dad1c5,
    #155837e0,
    #013220ec,
    #042d14e1,
    #001402ea
  );

  border: 0px;
  background-size: 200%;
  background-position: 0%;
  border-radius: 7px;
  cursor: pointer; /* Add cursor style */
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.9);
}

.formContain input[type="submit"]:hover {
  animation: glower 400ms 1 linear;
  box-shadow: 2px 2px 9px rgba(0, 0, 0, 1);
}

.formContain input[type="submit"]:focus {
  animation: glower 400ms 1 linear;
  box-shadow: 2px 2px 9px rgba(0, 0, 0, 1);
}

/* Keyframes for Button Hover Effect */
@keyframes glower {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 200%;
  }
}

@media (max-width: 480px) {
  .contain {
    width: 90%;
    aspect-ratio: 9/11;
  }
  .children {
    width: 210px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 2% 5%;
    margin-right: calc(50% - 105px - 8%);
    font-size: 3vw;
  }
  .formContain input[type="submit"] {
    position: absolute;
    bottom: 5%;
    left: 14%;
    font-size: 2.8vw;
  }
  .children[type="textarea"] {
    aspect-ratio: 4/2;
    position: initial;
  }
  .selectContain {
    width: 28%;
  }
  .thisclass select {
    width: 30vw;
    height: 6vw;
  }
}
@media (max-width : 400px) {
  .children{
    width: 180px;
    margin-right: calc(42% - 90px);
  }
  .contain{
    aspect-ratio: 9/12;
  }
}
@media (min-width: 880px) {
  .contain {
    width: 80%;
    aspect-ratio: 3/2;
  }
  .children {
    width: 35%;
    display: inline;
    aspect-ratio: 6/1;
    font-size: 1.3vw;
    margin: 3% 5%;
  }
  .thisclass .selectContain {
    width: 22%;
  }
  .children[type="textarea"] {
    aspect-ratio: 6/7;
    position: absolute;
    left: 8%;
    top: 30%;
  }
  .formContain input[type="submit"] {
    position: absolute;
    bottom: 40%;
    left: 60%;
    width: 200px;
    aspect-ratio: 5/1;
    font-size: 1.2vw;
  }
  .thisclass select {
    width: 13vw;
    height: 3vw;
    font-size: 1.3vw;
  }
}

/*____________________________________ */

/* Logo Container */
#myLogo {
  position: absolute;
  bottom: 3%;
  width: 100%;
  height: 50%;
  text-align: center;
}
/* Logo Text */
#myLogo p {
  font-family: "Times New Roman", Times, serif;
  font-weight: bolder;
  color: #06244df1;
  margin: 6% 10%;
  font-size: 3.2vw;
}
#myLogo p:first-child {
  margin-top: 5%;
}
#myLogo p:last-child {
  margin-top: 10%;
  font-size: 2vw;
  color: #06081e;
}
/* Responsive Adjustments */

@media (max-width: 480px) {
  #myLogo {
    height: 65%;
  }
  #myLogo p {
    margin: 10%;
    font-size: 7vw;
  }
  #myLogo p:first-child {
    margin-top: 60%;
  }
  #myLogo p:last-child {
    margin-top: 20%;
    font-size: 4vw;
  }
}
@media (min-width: 880px) {
  #myLogo {
    height: 65%;
  }
  #myLogo p {
    margin: 10%;
    font-size: 4vw;
  }
  #myLogo p:first-child {
    margin-top: 5%;
  }
  #myLogo p:last-child {
    margin-top: 10%;
    font-size: 2vw;
  }
}

/*__________________________________________________________________ */
/* select buttons */
/* 3D Button Styles */
.formButton {
  width: 70%;
  aspect-ratio: 16/5;
  margin: 15% 15%;
  border: none;
  font-size: 1.6vw; /* Slightly smaller font size */
  font-family: -apple-system , Sahel-Bold , sans-serif;
  background: linear-gradient(to bottom, #f9f9f9, #4762a360);
  color: #090c23;
  transition: all 200ms ease;
  transition: font-size 300ms ease;
  cursor: pointer;
  font-weight: 600;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
}

/* Hover Effect */
.formButton:hover {
  font-size: 1.7vw; /* Slightly larger font size on hover */
  background: linear-gradient(to bottom, #a9b3ff, #5575be60);
  box-shadow: 0px 0px 13px 8px rgba(0, 0, 0, 0.1);
}

/* Focus Effect */
.formButton:focus {
  border-left: 3px solid #090c23;
  outline: none;
}

/* Optional: Add box shadow for active state */
.formButton:active {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

/* make it responsive */
@media (max-width: 480px) {
  .formButton {
    font-size: 2.2vw;
    width: 80%;
    aspect-ratio: 3/1;
    margin-right: 12%;
  }
  .formButton:first-child{
    margin-top: 30%;
  }
  .formButton:hover {
    font-size: 2.4vw;
  }
  .formButton:focus {
    font-size: 2.6vw;
    border-radius: 5px;
    border-left: 2px solid #090c23;
  }
}
@media (min-width: 880px) {
  .formButton {
    font-size: 1.3vw;
  }
  .formButton:hover {
    font-size: 1.5vw;
  }
  .formButton:focus {
    font-size: 1.5vw;
    border-radius: 10px;
    border-left: 4px solid #090c23;
  }
}
#form2 .children {
