#changer{
    display: block;
    position: relative;
    width: 330px;
    height: calc(330px / 6);
    margin: 0px auto;
    margin-bottom: 10vw;
    box-sizing: border-box;
    border-radius: 30px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.825);
    background: radial-gradient( #d727a8d5 , #b30095);
  }
  #changer:hover{
    cursor: pointer;
  }
  #cTxt{
    display: flex;
    justify-content: space-between;
    margin: 0px 19px;
    height: 100%;
    z-index: 10;
  }
  #cTxt h3{
    font-family: Dirooz , Parastoo;
    -webkit-font-smoothing: antialiased; /* Set font smoothing */
    text-rendering: optimizeLegibility; /* Improve text rendering */
    -webkit-font-family: Dirooz;
    font-size: 20px;
    margin-top: 8px;
    z-index: 10;
    color: #280621 ;
    transition: all 300ms linear;
  }
  #cBtn{
    position: absolute;
    z-index: 5;
    height: 80%;
    border: 1px solid black;
    margin-top: 4px;
    width: 44%;
    border-radius: 30px;
    background-color: black;
    filter: opacity(0.8);
    transition: all 300ms linear;
  }
  /* make it responsive */
  @media(max-width : 580px){
    #changer{
        width: 160px;
        height: calc(160px / 6);
    }
    #cTxt h3{
        font-size: 10px;
        margin-top: 4px;
        font-family:  -apple-system, BlinkMacSystemFont, Dirooz;
    }
    #cTxt{
        margin: 0 7px;
        margin-left: 9px;
    }
    #cBtn{
        margin-top: 1.8px;
    }
  }

  