.thisclass{
  position: relative;
}
/* first part */
#fbox {
  width: 65%;
  aspect-ratio: 10/7;
  direction: rtl;
  font-family: Dirooz;
  font-size: 1.4vw;
  margin: 0 auto;
  position: relative;
  margin-bottom: 8vw;
}
.textContent {
  width: 60%;
  margin-right: 2%;
} 

#img1 {
  width: 25%;
  aspect-ratio: 1/1;
  background-color: rgb(235, 235, 235);

  position: absolute;
  left: 2%;
  top: 5%;
  border-radius: 15px;
  text-align: center;
  overflow: hidden;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.658);
}

#img2 {
  width: 25%;
  aspect-ratio: 1/1;
  background-color: rgb(235, 235, 235);
  position: absolute;
  left: 2%;
  top: 50%;
  border-radius: 15px;
  text-align: center;
  overflow: hidden;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.658);
}
#img1 div, #img2 div{
  width: 100%;
  height: 100%;
  position: relative;
}
#img1 img, #img2 img {
  width: 100%;
  height: 180%;
  position: absolute;
  right: 0%;
  top: 0%;
  transition: all 1000ms linear;
}
#img1 img:hover , #img2 img:hover{
  top: -80%;
  cursor: pointer;
}
#img1 img:active , #img2 img:active{
  bottom: -80%;
  cursor: pointer;
}
#fbox h5 {
  transform: scaleY(1.2);
  text-shadow: 0px 0px 2px black;
}
.fbtn {
  background: linear-gradient(to right, #dd2aad, #91067e, #cc1622, #c50091);
  background-size: 270% 100%;
  width: 35%;
  aspect-ratio: 4/1;
  font-size: 1.3vw;
  font-family: Parastoo-Bold;
  border: none;
  border-radius: 20px;
  box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.874);
  transition: all 500ms ease-in-out;
}
.fbtn:hover {
  cursor: pointer;
  box-shadow: -1px 1px 10px rgba(0, 0, 0, 1);
  background-position-x: 100%;
}
@keyframes flash {
  from {
    background-position-x: 0%;
  }
  to {
    background-position-x: 200%;
  }
}

/* second part */
#sbox {
  width: 65%;
  aspect-ratio: 10/7;
  direction: rtl;
  font-family: Dirooz;
  font-size: 1.4vw;
  margin:0 auto;
  position: relative;
  margin-bottom: 150px;
}

#seoimg {
  width: 33%;
  aspect-ratio: 4/5;
  background-color: rgb(235, 235, 235);
  position: absolute;
  left: 0%;
  top: 10%;
  border-radius: 15px;
  text-align: center;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.658);
}
/* icon */
#ic {
  transform: scale(1.5);
  margin-left: calc(50% - 35px);
  margin-bottom: 3%;
  background-image: linear-gradient(
    to left,
    #250b30,
    #411253,
    #840d61,
    #b81f6b,
    #d6517f,
    #d61674,
    #980c68,
    #660a46,
    #531253,
    #250b30
  );
  background-size: 1500% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: flasher 70s infinite linear;
}
@keyframes flasher {
  from {
    background-position: 0%;
  }
  to {
    background-position: 1500%;
  }
}
/* make it responsive */
@media (max-width: 1180px) {
  #sbox{
    margin-bottom: 100px;
  }

}
@media (max-width: 880px) {
  #sbox,
  #fbox {
    width: 75%;
    font-size: 1.6vw;
  }
}
@media (max-width: 760px) {
  #sbox,
  #fbox {
    font-size: 1.9vw;
  }

  #img1,
  #img2 {
    width: 30%;
    left: 1%;
  }
  #img1 {
    top: 5%;
  }
  #img2 {
    top: 55%;
  }
  #ic{
    margin-bottom: 5%;
  }
}
@media (max-width: 700px) {
  #sbox,
  #fbox {
    margin: 90px auto;
  }
  #fbox {
    margin-bottom: 130px;
  }

}
@media (max-width: 480px) {
  #sbox,
  #fbox {
    font-size: 2.5vw;
    width: 80%;
    margin: 60px auto;
  }

  #img1{
    width: 35%;
    top: 10%;
    left: -1%;
  }
  #img2{
    width: 35%;
    top: 50%;
    left: -1%;
  }
  #seoimg{
    width: 37%;
    left: -6%;
    top: 15%;
  }
  .fbtn{
    width: 45%;
    font-size: 2.3vw;
    border-radius: 7px;
    box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.874);
  }
  .fbtn:hover{
    box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.974);
  }
  #sbox{
    margin-top: 100px;
  }
}
