.thisclass{
    width: 15%;
    background-size: 200%;
    border: none;
    height: 40px;
    font-weight: bold;
    border-radius: 5px;
    font-family:Dirooz ;
    font-size: 1.3vw;
}
.thisclass:hover{
    cursor: pointer;
    animation: glower 500ms linear;
    box-shadow: 0px 0px 4px 1px gray;
}
.thisclass:focus{
    cursor: pointer;
    animation: glower 500ms linear;
    box-shadow: 0px 0px 4px 1px gray;
}
#blue{
    background: linear-gradient(90deg, #3aafa9 ,#3aafa9,#3aafa9,#3aafa9,#3aafa9, #cccce0,white ,#cccce0 , #3aafa9);
    background-size: 200%;
}
@keyframes glower{
    from{background-position: 0%;}
    to{background-position: 200%;}
}