#fbox {
  width: 65%;
  aspect-ratio: 10/7;
  direction: rtl;
  font-family: Dirooz;
  font-size: 1.4vw;
  margin: 0 auto;
  position: relative;
  margin-bottom: 8vw;
  padding: 1%;
  white-space: normal;
}
#inbox h4 {
  text-align: center;
}
#imgbox {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  margin: 1vw auto;
}
.imgs {
  width: 285px;
  aspect-ratio: 3/5;
  text-align: center;
  border-radius: 10px;
  background-color: rgb(241, 241, 241);
  filter: drop-shadow(1px 2px 3px rgba(0, 0, 0, 0.548));
}

.fbtn {
  background: linear-gradient(to right, #dd2aad, #91067e, #cc1622, #c50091);
  background-size: 270% 100%;
  width: 20%;
  aspect-ratio: 4/1;
  font-size: 1.3vw;
  font-family: Parastoo-Bold;
  border: none;
  border-radius: 20px;
  box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.874);
  transition: all 500ms ease-in-out;
}
.fbtn:hover {
  cursor: pointer;
  box-shadow: -1px 1px 10px rgba(0, 0, 0, 1);
  background-position-x: 100%;
}
#ic {
  transform: scale(1);
  margin: 0px 43%;
  margin-bottom: 5%;
  background-image: linear-gradient(
    to left,
    #0f133e,
    #411253,
    #840d61,
    #b81f6b,
    #d6517f,
    #d61674,
    #980c68,
    #190a46,
    #000930,
    #1a1c63,
    #364595,
    #0767a5,
    #0d5e9b,
    #003973,
    #001b43
  );
  background-size: 1500% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: flasher 70s infinite linear;
}
@keyframes flasher {
  from {
    background-position: 0%;
  }
  to {
    background-position: 1500%;
  }
}
/* make it responsive */

@media (max-width: 1300px) {
  #fbox {
    margin-top: 14vw;
    font-size: 1.6vw;
  }
}
@media (max-width: 1200px) {
  .imgs {
    width: 260px;
  }
}
@media (max-width: 1100px) {
  #imgbox {
    width: 100%;
  }
  #fbox {
    font-size: 1.8vw;
    margin-top: 12vw;
  }
  .fbtn {
    width: 27%;
    font-size: 1.7vw;
    border-radius: 25px;
  }
}
@media (max-width: 880px) {
  #fbox {
    font-size: 2vw;
    width: 75%;
  }
  .imgs {
    width: 220px;
  }
}
@media (max-width: 780px) {
  #fbox {
    font-size: 2.3vw;
  }
  .fbtn {
    font-size: 2.1vw;
  }
}
@media (max-width: 680px) {
  .imgs {
    width: 180px;
  }
}
@media (max-width: 580px) {
  #fbox {
    font-size: 2.5vw;
  }
  .imgs {
    width: 140px;
  }
  .fbtn {
    width: 32%;
    font-size: 2.4vw;
  }
}
@media (max-width: 480px) {
  #fbox {
    font-size: 2.8vw;
  }
  .imgs {
    border-radius: 7px;
  }
}
@media (max-width: 400px) {
  .imgs {
    width: 120px;
  }
  #fbox {
    width: 80%;
    font-size: 3vw;
  }
}
@media (max-width: 380px) {
  .imgs {
    width: 100px;
  }
  #fbox h4 {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
  }
  #fbox b {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
  }
}
