.thisclass {
  margin: auto auto;
  width: 80%;
  aspect-ratio: 5/4;
  position: relative;
  margin-bottom: 50px;
  border-radius: 10px;
  /*box-shadow: 0px 0px 5px 2px gray;*/
}
.thisclass p {
  font-size: 2.2vw;
  font-family: Dirooz;
  width: 42%;
  position: absolute;
  right: 3%;
  text-indent: 20px;
  top: 15%;
  text-align: right;
  padding: 10px;
  white-space: normal;
}
.thisclass h2 {
  font-family: -apple-system, Parastoo-Bold;
  position: absolute;
  right: 2%;
  font-size: 2.8vw;
  color: #1d5250;
}
#div1 {
  position: absolute;
  aspect-ratio: 1/1;
  left: 23%;
  top: 35%;
  z-index: 100;
  text-align: center;
  border-radius: 8px;
  background-color: #dfdfdf;
  filter: drop-shadow(-2px 2px 3px rgba(0, 0, 0, 0.466));
}
#div2 {
  position: absolute;
  width: 25%;
  aspect-ratio: 1/1;
  left: 10%;
  top: 15%;
  z-index: 90;
  text-align: center;
  border-radius: 8px;
  background-color: #dfdfdf;
  filter: drop-shadow(-2px 2px 3px rgba(0, 0, 0, 0.466));
}
#mbutton {
  position: absolute;
  right: 30%;
  border-radius: 20px;
  border: none;
  font-family: -apple-system , Sahel-Bold;
  bottom: 10%;
  width: 15%;
  aspect-ratio: 3/1;
  background: linear-gradient(to left , #116140 , #268ed4 , #268ed4, #01263fe5 );
  background-size: 200%;
  transition: all 300ms ease-out;
  font-size: 1.3vw;
  filter: drop-shadow(1px 1px 2px black);
}
#mbutton:hover{
    background-position: 100%;
    cursor: pointer;
    filter: drop-shadow(-2px 2px 4px rgba(0, 0, 0, 0.753));
}
@media (min-width: 880px) {
  .thisclass {
    width: 70%;
    aspect-ratio: 2/1;
  }
  .thisclass p {
    font-size: 1.4vw;
  }
  .thisclass h2 {
    font-size: 1.7vw;
  }
  #div1,
  #div2 {
    filter: drop-shadow(-3px 3px 4px rgba(0, 0, 0, 0.466));
    width: 25%;
  }
}
@media (max-width : 880px) {
    #mbutton{
        width: 20%;
        font-size: 2.4vw;
    }
    #div1, #div2{
      width: 30%;
    }
    #div2{
      top: 20%;
      left: 5%;
    }
    #div1{
      left: 20%;
    }
}
@media (max-width: 600px) {
  .thisclass {
    margin-top: 5%;
  }
}
@media (max-width: 480px) {
  .thisclass {
    width: 95%;
  }
  #mbutton{
    font-size: 3vw;
    top: 90%;
  }
  .thisclass p {
    font-size: 2.6vw;
    padding: 5px;
    width: 35%;
  }
  #div1,
  #div2 {
    filter: drop-shadow(-1px 1px 2px rgba(0, 0, 0, 0.466));
    width: 35%;
  }
  #div1{
    top: 38%;
  }
}

#div1 div , #div2 div{
  width: 100%;
  height: 100%;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}
#div1 img , #div2 img{
  position: absolute;
  right: 0;
  width: 100%;
  height: 100%;
}
