.thisclass{
    width: 100%;
    box-sizing: border-box;
    height: 50px;
    border: 1px solid black;
    background-color: black;
    color: #26d48c;
    border-top:2px solid #26d48ba9;
}
#container{
    padding: 5px;
    display: flex;
    height: 35px;

}
#justify{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 35px;
    width: 100%;
    margin:0px 40px;

}
#justify div{
    display: flex;
    align-items: center;
}
#location , #topemail{
    margin: auto 10px;
}
#topemail > a{
    text-decoration: none;
    color: white;
}
#location > a{
    text-decoration: none;
    color: white;
}
#location > a > i{
    color: #26d48c;
}
#topemail > a > i{
    color: #26d48c;
}
#icons a{
    color: #17242a;
    background-color: white;
    margin: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    text-decoration: none;
    border-radius: 50%;
}
#icons i{
    margin: auto;
    transform: scale(1.5);
}
#note{
    position: relative;
    overflow: hidden;
    width: 500px;
}
#mymarquee{
    position: absolute;
    animation: marquee 7s ease-in-out infinite;
    width: 400px;
}

@keyframes marquee {
    0% {
        transform: translateX(110%);
      }
      100% {
        transform: translateX(-130%);
      }
    
}

@media (max-width : 1345px) {
    #note{
        animation: none;
    }
}

@media (max-width : 1072px) {
    #note{
        width: 0px;
    }
}

@media (max-width : 763px) {
    .thisclass{
        display: none;
    }
}