.thisclass {
  display: block;
  direction: rtl;
  /*  margin-top: 4vw;
    aspect-ratio: 20/5.8;
  */
  margin-bottom: 6vw;
  height: calc(95vh - 95px);
  margin-top: 0px;
  background: linear-gradient(15deg, #fff 40%, #a3c6df);
  background-position: fixed;
  min-height: 38vw;
  /*  max-height: 55vw;*/
  z-index: 1;
  display: flex;
  position: relative;
}
#textCover {
  aspect-ratio: 1/1;
  position: absolute;
  top: 3%;
  height: 531px;
  right: 8%;
  z-index: 2;
  border-radius: 15px;
}
#headp {
  z-index: 7;
  font-family: Parastoo-Bold;
  font-size: 2.1vw;
  width: 91%;
  right: 1%;
  top: 42%;
  text-align: center;
  position: absolute;
  background-image: linear-gradient(to bottom, #0bb36d9a, black 90%);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.thisclass p {
  font-family: Dirooz;
  font-size: 1.8vw;
  margin-top: 15%;
  width: 38%;
  white-space: normal;
  margin-right: 53%;
  animation: shadowMove 2s infinite ease;
}

#animationdiv {
  width: 28%;
  aspect-ratio: 1/1;
  box-shadow: -2px 5px 5px rgb(0, 0, 0, 0.8);
  border-radius: 40%;
  margin-top: 15px;
  margin-right: 25px;
  position: relative;
  overflow: hidden;
  display: none;
}
@keyframes rounder {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#bckimg {
  position: absolute;
  top: 3%;
  filter: hue-rotate(0deg) drop-shadow(3px 3px 5px rgba(0, 0, 0, 0.534)) brightness(95%) contrast(115%);
  height: 531px;
  right: 8%;
  z-index: 1;
  border-radius: 15px;
  aspect-ratio: 1/1;
}

#backup {
  background-color: #d2e6ef;
  filter: contrast(120%);
  border-radius: 15px;
  top: 6%;
  height: 531px;
  aspect-ratio: 1/1;
  position: absolute;
  right: 7%;
  box-shadow: 4px 6px 12px rgba(0, 0, 0, 0.534);
}
/* make it responsive */

@media (max-width: 1180px) {
  .thisclass p {
    margin-right: 59%;
    top: 11%;
    position: absolute;
  }
}
@media (max-width: 1020px) {
  .thisclass p {
    margin-right: 62%;
    width: 35%;
    font-size: 1.9vw;
  }
  #bckimg,
  #backup,
  #textCover {
    height: 450px;
  }
  #bckimg,
  #textCover {
    top: 8%;
  }
  #backup {
    top: 10%;
  }
  #headp {
    font-size: 2.8vw;
  }
}
@media (max-width: 860px) {
  .thisclass {
    height: 90vw;
  }
  #bckimg,
  #textCover {
    top: 5%;
    right: calc(48% - 225px);
  }
  #bckimg{
    filter: drop-shadow(0px 0px 6px rgba(0, 0, 0, 0.734)) brightness(95%) contrast(115%);
  }
  #backup {
    height: 482px;
    top: 3%;
    right: calc(46% - 225px);
    filter: contrast(105%);
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.534);

  }
  .thisclass p {
    top: 63%;
    width: 50%;
    text-align: center;
    left: 26%;
    font-size: 2.6vw;
  }
}
@media (max-width: 730px) {
  .thisclass {
    height: 110vw;
  }
  #bckimg,
  #textCover {
    height: 350px;
    right: calc(40% - 125px);
  }
  #backup {
    height: 380px;
    top: 3%;
    right: calc(38% - 125px);
  }
}
@media (max-width: 510px) {
  #headp {
    font-size: 4vw;
  }
  #backup {
    right: calc(37% - 125px);
  }
  .thisclass p {
    top: 69%;
  }
}
@media (max-width: 480px) {
  .thisclass{
    background: linear-gradient(15deg, #fff 10%, #a3c6df);
  }
  #bckimg,
  #textCover {
    height: 280px;
    right: calc(50% - 140px);
  }
  #bckimg{
    filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.734)) brightness(95%) contrast(115%);
  }
  #backup {
    height: 295px;
    top: calc(3% + 3px);
    right: calc(48% - 140px);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.534);

  }
  #headp {
    font-size: 3.6vw;
  }
  .thisclass p {
    font-size: 3.3vw;
    top: 64%;
  }
}
@media(max-width : 380px){
  .thisclass{
    height: 130vw;
  }
}
@media(max-width: 350px){
  .thisclass{
    background: linear-gradient(25deg, #fff 22%, #83bce4d7);
  }
  #bckimg,
  #textCover {
    height: 220px;
    right: calc(50% - 110px);
  }
  #bckimg{
    filter: drop-shadow(0px 0px 3px rgba(0, 0, 0, 0.734)) brightness(95%) contrast(115%);
  }
  #backup {
    height: 235px;
    top: calc(3% + 3px);
    right: calc(48% - 111px);
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.534);

  }
  #headp {
    font-size: 3.6vw;
    top: calc(42% + 4px);
  }
}
