.thisclass{
    margin: 10vw auto;
    width: 70%;
    font-size: 2vw;
    font-family: Dirooz;
    direction: rtl;
}
.thisclass p{
    white-space: normal;
}
.thisclass b{
    text-decoration: underline;
}
@media(min-width:940px){
    .thisclass{
        width: 60%;
        font-size: 1.5vw;
    }
}
@media(max-width: 480px){
    .thisclass{
        width: 86%;
        font-size: 2.4vw;
    }
}