.thisclass {
  background-size: calc(100% * 2) calc(100% * 2);
  width: 100%;
  aspect-ratio: 16/7;
  position: relative;
  background-position-x: right;
}
/* pages */
.firstpage {
  background-color: #aec7df;
  transition: all 300ms ease-out;
}
.secondpage {
  background-color: #cfece9;
  transition: all 300ms ease-out;
}
#page {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 80% 100%;
  right: 0%;
  overflow: hidden;
}
#innerpage {
  width: 90%;
  height: 80%;
  margin: 3% 5%;
  overflow: hidden;
  position: relative;
  box-shadow: -1px 2px 1.2em rgba(0, 0, 0, 0.877);
  background-size: 200%;
  border-radius: 15px;
}
/* images */
#simg {
  position: absolute;
  width: 40%;
  height: 100%;
  filter: drop-shadow(0px 0px 20px black);
  z-index: 500;
}
.secondpage #simg {
  left: 2%;
  animation: LeftPicin 500ms 1 ease-out;
}
.firstpage #simg {
  position: absolute;
  animation: LeftPicout 500ms 1 ease-in;
  left: -50%;
}
#fimg {
  position: absolute;
  height: 100%;
  top: 0;
  width: 40%;
  filter: drop-shadow(14px 10px 25px black);
  transform: rotateY(180deg);
  z-index: 500;
}
.firstpage #fimg {
  right: 0%;
  animation: RightPicin 500ms 1 ease-out;
}
.secondpage #fimg {
  position: absolute;
  animation: RightPicout 500ms 1 ease-in;
  right: -50%;
}

/* __________________________________________ */
@keyframes LeftPicout {
  0% {
    opacity: 1;
    left: 2%;
  }
  100% {
    opacity: 0;
    left: -50%;
  }
}
@keyframes RightPicout {
  0% {
    opacity: 1;
    right: 0%;
  }
  100% {
    opacity: 0;
    right: -50%;
  }
}

@keyframes LeftPicin {
  0% {
    opacity: 0;
    left: -50%;
  }
  100% {
    opacity: 1;
    left: 2%;
  }
}
@keyframes RightPicin {
  0% {
    opacity: 0;
    right: -50%;
  }
  100% {
    opacity: 1;
    right: 0%;
  }
}
/* buttons */
#leftBtn {
  position: absolute;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border: 4px solid #3498db;
  border-radius: 17px;
  color: #3498db;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  width: 10px;
  aspect-ratio: 10/6;
}
#leftBtn:hover {
  background-color: #3498db;
  color: #ffffff;
}
#leftBtn:active,
#rightBtn:active {
  background-color: #3498db;
  color: #ffffff;
}
#rightBtn {
  position: absolute;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border: 4px solid #3498db;
  border-radius: 17px;
  color: #3498db;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  width: 10px;
  height: 15px;
}
#rightBtn:hover {
  background-color: #3498db;
  color: #ffffff;
}
@media (min-width: 600px) {
  #rightBtn,
  #leftBtn {
    top: 1%;
  }
}
@media (max-width: 600px) {
  #rightBtn,
  #leftBtn {
    transform: scale(0.5);
  }
}
@media (max-width: 500px) {
  #rightBtn,
  #leftBtn {
    top: -2%;
  }
}
@media (max-width: 450px) {
  #rightBtn,
  #leftBtn {
    display: none;
  }
}
/* __________________________________________ */

.firstpage #leftBtn {
  left: 1%;
  animation: LeftBtnin 500ms 1 linear;
}
.secondpage #leftBtn {
  left: -50%;
  animation: LeftBtnout 500ms 1 linear;
}
.secondpage #rightBtn {
  right: 1%;
  animation: RightBtnin 500ms 1 linear;
}
.firstpage #rightBtn {
  right: -50%;
  animation: RightBtnout 500ms 1 linear;
}
@keyframes LeftBtnout {
  0% {
    opacity: 1;
    left: 1%;
  }
  100% {
    opacity: 0;
    left: -50%;
  }
}
@keyframes RightBtnout {
  0% {
    opacity: 1;
    right: 2%;
  }
  100% {
    opacity: 0;
    right: -50%;
  }
}
@keyframes LeftBtnin {
  0% {
    opacity: 0;
    left: -50%;
  }
  100% {
    opacity: 1;
    left: 1%;
  }
}
@keyframes RightBtnin {
  0% {
    opacity: 0;
    right: -50%;
  }
  100% {
    opacity: 1;
    right: 1%;
  }
}
/* __________________________________________ */
/* icons */

#ficon {
  width: 20%;
  position: absolute;
  top: 1%;
}
.firstpage #ficon {
  right: 33%;
  animation: ToRight 2000ms 1 ease-out;
}
.secondpage #ficon {
  right: 50%;
  animation: ToLeft 2000ms 1 ease-out;
}
@keyframes ToLeft {
  0% {
    right: 33%;
  }
  100% {
    right: 50%;
  }
}
@keyframes ToRight {
  0% {
    right: 50%;
  }
  100% {
    right: 33%;
  }
}
/* __________________________________________ */
/* texts */
/* mutual page */
#innerpage {
  font-size: 1.1vw;
}
#innerpage #s1item {
  position: absolute;
  right: 15%;
  font-style: italic;
}
#innerpage #s2item {
  position: absolute;
  right: 8%;
  direction: rtl;
}
#innerpage #s3item {
  position: absolute;
  right: 8%;
  top: 41%;
}
#innerpage #s4item {
  position: absolute;
  right: 5%;
  top: 52%;
  direction: rtl;
  width: 50%;
  list-style-type: disc;
}
#innerpage #f1item {
  position: absolute;
  left: 25%;
  font-style: italic;
}
#innerpage #f2item {
  position: absolute;
  left: 12%;
  direction: rtl;
}
#innerpage #f3item {
  position: absolute;
  left: 40%;
  top: 40%;
  direction: rtl;
}
#innerpage #f4item {
  position: absolute;
  left: 2%;
  top: 52%;
  direction: rtl;
  width: 55%;
  list-style-type: disc;
}
@media(max-width:500px){
  #innerpage #s4item{
    right: -2%;
  }
}
/* __________________________________________ */
/* item1 */
.firstpage #s1item {
  animation: Text1out 500ms 1 ease-in;
  top: -50%;
}
.secondpage #s1item {
  animation: Text1in 500ms 1 ease-out;
  top: 5%;
}
.secondpage #f1item {
  animation: Text1out 500ms 1 ease-in;
  top: -50%;
}
.firstpage #f1item {
  animation: Text1in 500ms 1 ease-out;
  top: 5%;
}
@keyframes Text1in {
  0% {
    top: -50%;
  }
  100% {
    top: 5%;
  }
}
@keyframes Text1out {
  0% {
    top: 5%;
  }
  100% {
    top: -50%;
  }
}
/* item2 */
.firstpage #s2item {
  animation: Text2out 700ms 1 ease-in;
  top: -50%;
}
.secondpage #s2item {
  animation: Text2in 700ms 1 ease-out;
  top: 25%;
}
.secondpage #f2item {
  animation: Text2out 700ms 1 ease-in;
  top: -50%;
}
.firstpage #f2item {
  animation: Text2in 700ms 1 ease-out;
  top: 25%;
}
@keyframes Text2in {
  0% {
    top: -50%;
  }
  100% {
    top: 25%;
  }
}
@keyframes Text2out {
  0% {
    top: 25%;
  }
  100% {
    top: -50%;
  }
}
/* __________________________________________ */
/* item 3,4 */
.firstpage #s3item {
  animation: Text34out 400ms 1 ease-in;
  opacity: 0;
}
.firstpage #s4item {
  animation: Text34out 400ms 1 ease-in;
  opacity: 0;
}
.secondpage #s3item {
  animation: Text34in 1000ms 1 ease-out;
  opacity: 1;
}
.secondpage #s4item {
  animation: Text34in 1200ms 1 ease-out;
  opacity: 1;
}

.secondpage #f3item {
  animation: Text34out 400ms 1 ease-in;
  opacity: 0;
}
.secondpage #f4item {
  animation: Text34out 400ms 1 ease-in;
  opacity: 0;
}
.firstpage #f3item {
  animation: Text34in 1000ms 1 ease-out;
  opacity: 1;
}
.firstpage #f4item {
  animation: Text34in 1200ms 1 ease-out;
  opacity: 1;
}
@keyframes Text34in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes Text34out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* __________________________________________ */

/* chat boxes */
/* first one */
.cbox {
  position: absolute;
  width: 10%;
  aspect-ratio: 1/1;
  z-index: 200;
}
.cbox div {
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
}
.cbox .mimg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: -4%;
  filter: invert(1) drop-shadow(4px 3px 12px black);
  opacity: 0.7;
}
.cbox p {
  position: absolute;
  top: 30%;
  width: 70%;
  left: 15%;
  font-family: -apple-system , Sahel-bold;
}
#fcbox {
  top: 46%;
  transform: scaleX(1.4) scaleY(1.8);
}
#fcbox .mimg {
  transform: rotateY(180deg);
}
#webtemp {
  position: absolute;
  width: 90%;
  right: 12%;
  top: 4%;
}
#fcbox p {
  font-size: 0.7vw;
  font-family: -apple-system , Parastoo-Bold;
  left: 7%;
  top: 28%;
}
.firstpage #fcbox {
  right: 28%;
  animation: Fcboxin 700ms ease-out;
}
.secondpage #fcbox {
  right: -50%;
  animation: Fcboxout 500ms ease-in;
}
@keyframes Fcboxout {
  0% {
    right: 28%;
  }
  100% {
    right: -60%;
  }
}
@keyframes Fcboxin {
  0% {
    right: -60%;
  }
  100% {
    right: 28%;
  }
}
/* __________________________________________ */
/* second one */
#scbox {
  top: 35%;
  left: 16%;
  z-index: 600;
}
#scbox .mimg {
  transform: scaleX(1.5);
  filter: invert(1) drop-shadow(0px 0px 15px white);
}
#scbox p {
  font-family: -apple-system , Parastoo-Bold;
  direction: rtl;
  top: 27%;
  width: 80%;
}
.secondpage #scbox {
  left: 16%;
  animation: Scboxin 700ms ease-out;
}
.firstpage #scbox {
  left: -60%;
  animation: Scboxout 300ms ease-in;
}
@keyframes Scboxout {
  0% {
    left: 16%;
  }
  100% {
    left: -60%;
  }
}
@keyframes Scboxin {
  0% {
    left: -60%;
  }
  100% {
    right: 16%;
  }
}
