.thisClass {
  position: relative;
}
/* icon */
#ic {
  transform: scale(1.5);
  margin-left: calc(50% - 15px);
  margin-bottom: 3%;
  background-image: linear-gradient(
    to left,
    #0f133e,
    #411253,
    #840d61,
    #b81f6b,
    #d6517f,
    #d61674,
    #980c68,
    #190a46,
    #000930,
    #1a1c63,
    #364595,
    #0767a5,
    #0d5e9b,
    #003973,
    #001b43
  );
  background-size: 1500% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: flasher 70s infinite linear;
}
@keyframes flasher {
  from {
    background-position: 0%;
  }
  to {
    background-position: 1500%;
  }
}
/* first part */
#fbox {
  width: 65%;
  aspect-ratio: 10/7;
  direction: rtl;
  font-family: Dirooz;
  font-size: 1.4vw;
  margin: 0 auto;
  position: relative;
  margin-bottom: 8vw;
}
#fdiv{
    position: relative;
}
#fdiv p{
    width: 50%;
}

#fdiv img{
    width: 45%;
    position: absolute;
    top: -10%;
    left: 0;
    border-radius: 0% 35% 55px;
}
.ldiv{
    display: flex;
}
.ldiv p{
    margin-right: 2%;
    font-size: 1.1vw;
}
.ldiv button{
    background: linear-gradient(to right, #dd2aad, #91067e, #cc1622, #c50091);
    background-size: 270% 100%;
    width: 20%;
    aspect-ratio: 4/1;
    height: 2%;
    margin: auto 1%;
    margin-right: 3%;
    font-size: 1.3vw;
    font-family: Parastoo-Bold;
    border: none;
    border-radius: 20px;
    box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.874);
    transition: all 500ms ease-in-out;
  }
  .ldiv button:hover{
    cursor: pointer;
    box-shadow: -1px 1px 10px rgba(0, 0, 0, 1);
    background-position-x: 100%;
  }
  /* second part */
  #sbox{
    width: 65%;
    aspect-ratio: 10/7;
    direction: rtl;
    font-family: Dirooz;
    font-size: 1.4vw;
    margin: 0 auto;
    position: relative;
    margin-bottom: 8vw;
  }
  #sbox h2{
    width: 100%;
    text-align: center;
    background-image: linear-gradient(
        to left,
        #0f133e,
        #411253,
        #840d61,
        #b81f6b,
        #d6517f,
        #d61674,
        #980c68,
        #190a46,
        #000930,
        #1a1c63,
        #364595,
        #0767a5,
        #0d5e9b,
        #003973,
        #001b43
      );
      background-size: 1500% 100%;
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      animation: flasher 70s infinite linear;
      filter: brightness(50%);
    }
    #sbox #img1{
        width: 35%;
        position: absolute;
        left: 0;
        animation: scaleUpDown 4s infinite;
        filter: drop-shadow(-2px 2px 2px black);
        top: 2%;
    }
    #sbox #img2{
        width: 40%;
        position: absolute;
        left: -2%;
        animation: scaleUpDown 4s infinite 2s;
        filter: drop-shadow(-2px 2px 2px black);
    }
    #kharid p{
        width: 55%;
    }
    #sbox .whyus{
        width: 100%;
    }
    @keyframes scaleUpDown {
        0%, 100% {
          transform: scale(1);
        }
        50% {
          transform: scale(1.2);
        }
      }
      #sbox #img3{
        width: 45%;
        position: absolute;
        top: 57%;
        left: 5%;
        border-radius: 0% 35% 55px;
        filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.585));
    }
    #sbox .f2p{
        width: 30%;
        margin-right: 10%;
    }

  /* make it responsive */
  @media (max-width : 1300px) {
    .thisClass{
        margin-top: 10%;
    }
    #ic{
        margin-bottom: 6%;
    }
    #fbox , #sbox{
        font-size: 1.7vw;
    }
    #fdiv img{
        top: 3%;
    }
    #ldiv p{
        font-size: 1.3vw;
    }
    #sbox .f2p{
        width: 38%;
        margin-right: 5%;
    }
    #sbox #img3{
        top: 59%;
    }
  }
  @media (max-width : 1100px) {
    #sbox #img1{
        width: 40%;
    }
    #sbox #img2{
        width: 45%;
    }
  }
  @media (max-width : 880px) {
    .thisClass{
        margin-top: 0%;
    }
    #fbox , #sbox{
        font-size: 2.2vw;
        width: 80%;
    }
    #sbox #img3{
        width: 50%;
    }
    .ldiv button{
        width: 25%;
        font-size: 2vw;
        margin-right: 1%;
    }
    .ldiv p{
        font-size: 1.7vw;
    }
  }
  @media (max-width : 680px){
    #fbox , #sbox{
        font-size: 2.5vw;
        width: 85%;

    }
    #fdiv p{
        width: 40%;
    }
    #fdiv img{
        width: 60%;
        top: 15%;
        transform: scaleY(1.27);
        left: -3%;
    }
    #ic{
        margin-bottom: 10%;
        transform: scale(1.1);
        margin-left: calc(50% - 25px);
    }
  }
  @media (max-width : 580px) {
    #fdiv img{
        width: 50%;
        left: 0;
        top: 8%;
    }
    #fdiv p{
        width: 100%;
    }
    #fdiv p:first-child{
        width: 50%;
    }
    #fbox , #sbox{
        font-size: 3.2vw;
    }
    .ldiv button{
        width: 30%;
        font-size: 2.8vw;
        font-family: -apple-system, BlinkMacSystemFont, Dirooz;
    }
    .ldiv p{
        font-size: 2.2vw;
    }
    #kharid #midp{
        width: 100%;
    }
    #sbox #img1{
        width: 35%;
        top: 3%;
        left: 8%;
    }
    #sbox #img2{
        width: 60%;
        top: 17%;
        left: -4%;
    }
    #sbox #img3{
        width: 50%;
        transform: scaleY(1.2);
        top: 62%;
        left: -2%;
        filter: drop-shadow(-1px 1px 1px rgba(0, 0, 0, 0.481));
    }
    #furush .f2p{
        width: 50%;
        margin-right: 0px;
    }
  }
  @media (max-width : 450px) {
    .ldiv button{
        width: 35%;
    }
  }