.thisclass {
  width: 80%;
  margin: 80px auto;
  aspect-ratio: 3/1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.thisclass div {
  width: 19%;
  aspect-ratio: 4/6;
  margin-top: 2%;
  text-align: center;
  border-radius: 15px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.911);
  transition: all 300ms ease-out;
  position: relative;
  direction: rtl;
}
#breaker {
  width: 0px;
  height: 0px;
}
#div3,
#div2,
#div4,
#div1 {
  background: linear-gradient(to top, #26d48c, #020c08, #268ed4, #01263fe5);
  background-size: 100% 290%;
  transition: all 700ms ease-out;
}
#div1:hover {
  background-position-y: 100%;
  color: #ede6d6;
}
#div2:hover {
  background-position-y: 100%;
  color: #ede6d6;
}
#div3:hover {
  background-position-y: 100%;
  color: #ede6d6;
}
#div4:hover {
  background-position-y: 100%;
  color: #ede6d6;
}

.thisclass div:hover {
  cursor: pointer;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.7);
}

.thisclass div:hover p {
  animation: flashText 1500ms ease-in-out infinite;
}
.thisclass div i {
  position: absolute;
  width: 70%;
  top: 2%;
  right: 13%;
  font-size: 9vw;
}
.thisclass div p {
  position: absolute;
  bottom: 5%;
  width: 100%;
  font-size: 1.5vw;
  font-family: Dirooz;
  text-align: center;
}
.thisclass div h3 {
  position: absolute;
  top: 38%;
  font-family: -apple-system, Sahel-Bold;
  width: 100%;
  font-size: 1.8vw;
  text-align: center;
}
@media (max-width: 880px) {
  .thisclass {
    width: 60%;
  }
  #breaker {
    flex-basis: 100%;
  }
  .thisclass div {
    width: 35%;
  }
}
@media (min-width: 880px) {
  .thisclass {
    width: 80%;
  }
  .thisclass div {
    width: 18%;
    height: 85%;
  }
  .thisclass div h3 {
    font-size: 1.4vw;
  }
  .thisclass div p {
    font-size: 1.2vw;
  }
  .thisclass div i {
    font-size: 7vw;
  }
}
@media (max-width: 680px) {
  .thisclass {
    width: 70%;
  }
  .thisclass div h3 {
    font-size: 2.3vw;
  }
  .thisclass div p {
    font-size: 1.7vw;
  }
  .thisclass div i {
    transform: scale(1.4);
    margin-top: 8%;
  }
}
@media (max-width: 480px) {
  .thisclass {
    width: 80%;
    margin-top: 20px;
  }
  .thisclass div {
    width: 35%;
  }
  .thisclass div:focus {
    width: 20%;
    margin-top: 2%;
    height: 85%;
  }
  .thisclass div h3 {
    font-size: 2.8vw;
  }
  .thisclass div p {
    font-size: 2.2vw;
  }
  #div1:active .p {
    animation: flashText 1500ms ease-in-out infinite;
  }
  #div1:focus .p {
    animation: flashText 1500ms ease-in-out infinite;
  }
}
@keyframes flashText {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
