.thisclass {
  white-space: nowrap;
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  position: relative;
  width: 80%;
  aspect-ratio: 6/2;
  margin: auto auto;
  margin-bottom: 20px;
  grid-gap: 15px 15px;
}

.newsItem {
  padding: 20px;
  background: linear-gradient(55deg, #188557 5%, #020c08 100%);
  box-shadow: -3px 4px 10px #1a1919;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  direction: rtl;
}

#i1 {
  grid-column: 1 / span 2;
  grid-row: 1 / span 6;
}
#i2 {
  grid-column: 3 / span 2;
  grid-row: 1 / span 3;
}
#i3 {
  grid-column: 3 / span 2;
  grid-row: 4 / span 3;
}
#i4 {
  grid-column: 5 / span 2;
  grid-row: 1 / span 6;
}
.imgBox {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  height: 100%;
  background: #d4d4d4;
  z-index: 100;
}
.imgBox img {
  width: 100%;
  height: 100%;
  opacity: 1;
  filter: brightness(30%);
  transition: all 300ms ease;
}
#i2 .imgBox img,
#i3 .imgBox img {
  opacity: 1;
  filter: brightness(70%);
  transform: scale(1.05);
}

#i2:hover .imgBox img,
#i3:hover .imgBox img {
  opacity: 1;
  filter: brightness(80%);
  transform: scale(1.1);
}
.newsItem:hover .imgBox img {
  opacity: 1;
  filter: brightness(60%);
  transform: scale(1.1);
}
.newsItem:hover {
  cursor: pointer;
}
.newsContent {
  width: 55%;
  margin: auto 6%;
  height: 85%;
  position: absolute;
  text-align: right;
  font-family: Dirooz;
  font-size: 1.1vw;
  white-space: normal;
  padding: 1%;
  z-index: 200;
}

/* Responsive adjustments */
@media (min-width: 880px) {
  .newsContent {
    font-size: 1.1vw;
  }
}
@media (max-width: 480px) {
  .newsContent {
    font-size: 1.8vw;
  }
  .thisclass {
    grid-gap: 10px;
  }
  #i2 .newsContent,
  #i3 .newsContent {
    margin: auto auto;
    top: 13%;
  }
}

/* Scrolling animation */

@media (max-width: 768px) {
  .thisclass {
    width: 90%;
    aspect-ratio: 6/5;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 5px;
  }
  .newsContent {
    font-size: 1.6vw;
  }
  #i1 {
    grid-column: 1 / span 3;
    grid-row: 1 / span 5;
  }
  #i2 {
    grid-column: 4 / span 3;
    grid-row: 1 / span 2;
  }
  #i3 {
    grid-column: 1 / span 3;
    grid-row: 6 / span 2;
  }
  #i4 {
    grid-column: 4 / span 3;
    grid-row: 3 / span 5;
  }
}
@media (max-width: 1100px) {
  .thisclass {
    grid-gap: 10px;
  }
}
#i2 {
  animation: waver 2s infinite;
  border: 2px solid black;
}

@keyframes waver {
  0%,
  100% {
    border-top-right-radius: 20% 40%;
    border-bottom-left-radius: 0%;
  }
  50%{
border-top-right-radius: 0%;
border-bottom-left-radius:20% 40% ;
  }
}
