#thisClass{
    border: 1px solid red;
    height: 51vh;
}
#thisClass form{
    width: 500px;
    aspect-ratio: 2/1;
    border: 1px solid green;
    margin: 3% auto;
    text-align: center;
    border-radius: 20px;
    background: linear-gradient(90deg , black , #135029);
}
#thisClass input{
    display: block;
    width: 50%;
    aspect-ratio: 20/3;
    margin: 6% auto;
    border-radius: 10px;
}
#thisClass input[type = "text"] , #thisClass input[type = "password"]{
    padding-left: 10px;
}
#thisClass input[type = "submit"]{
    width: 30%;
    aspect-ratio: 4/1;
    font-size: 1.4vw;
    background-color: white;
    border: none;
    transition: all 150ms linear;
}
#thisClass input[type = "submit"]:hover{
    cursor: pointer;
    box-shadow: 0px 0px 2px 2px white;
}