.thisclass{
    width: 100%;
    box-sizing: border-box;
    height: 80px;
    background-color: #3e91b8;
    margin-top: 0px;
    overflow: hidden;
    transition: all 300ms linear; 
    direction: rtl;
    z-index: 100;
}
#container{
    margin: 5px 40px;
    height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
#container > h1{
    display: flex;
    margin: auto 5px;
    height: 60px;
    display: flex;
    position: inherit;
    align-items: center;
    font-family: Dirooz , -apple-system , arial , sans-serif ;
    font-weight: 600;
    transform: scale(1.5 , 1.5);
    color: #17242a;
    top: 4%;
    right: 3%;
}
#container > h1 >p{
    color: white;
}
#includes{
    display: flex;
    flex-direction: row;
    width: 500px;
    align-items: center;
    justify-content: space-between;
    
}
#includes p{
    text-decoration: none;
    color: white;
    font-size: 1.4em;
    text-align: center;
    transition: color 200ms ease-in-out;
}
#includes div:first-child p{
    margin-right: 20%;
    margin-bottom: 20px;
    margin-top: 15px;
    font-weight: 600;
    font-family: -apple-system , Dirooz;
}
#includes p:hover{
    color: #17242a;
    cursor: pointer;
}

#animate{
    color: white;
    position: absolute;
    right: 130px;
    animation: myAnime 1000ms linear infinite;
}


#callinf{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 80%;
    left: 3%;
    transform: scale(0.9);
}
#hidericon p{
    display: flex;
    align-items: center;
}
#hidericon{
    display: none;
}
#hidericon p:hover{
    cursor: pointer; 
}



@media (max-width : 1100px){
    #includes{
        display: block;
        position: absolute;
        width: 200px;
        top: 0px;
        left: calc(50% - 100px);
    }
    #container > h1{
        right: 2%;
        transform: scaleX(0.9);
    }
    #hidericon{
        display: flex;
    }
    #container{
        margin: 5px 10px;
    
    }
    #callinf{
        left: 7%;
        transform: scale(0.8);
    }

}

@media (max-width :600px){
    #container > h1{
        transform: scale(0.6);
        right: -2%;
    }
    #callinf{
        transform: scale(0.6);
    }
}

@media (max-width :450px){
    #container > h1{
        position: absolute;
        right: calc(90% - 110px);
    }
    #callinf{
        display: none;
    }
    #includes{
        right: -5%;
    }
    #includes p{
        font-size: 1.2em;
    }
    #includes div:first-child p{
        margin-top: 18px;
    }
}


@keyframes myAnime {
    0% {
        transform: scaleX(1);
      }
      10%, 20% {
        transform: scale3d(.9,.9,.9) rotate(-3deg);
      }
      30%, 50%, 70%, 90% {
        transform: scale3d(1.1,1.1,1.1) rotate(3deg);
      }
      40%, 60%, 80% {
        transform: scale3d(1.1,1.1,1.1) rotate(-3deg);
      }
      100% {
        transform: scaleX(1);
      }
}
