.thisClass {
  width: 100%;
  direction: rtl;
  z-index: 3;
  position: relative;
  margin-top: 20%;
}
.contain {
  width: 65%;
  margin:0 17.5%;
  margin-bottom: 10vw;
  font-size: 1.6vw;
  font-family: Dirooz;
  padding: 1%;
  white-space: normal;
  border-radius: 20px;
  background: radial-gradient(white 100%, #ff31c871);
}
.contain b {
  font-size: 1.65vw;
}
.fbtn {
  background: linear-gradient(to right, #dd2aad, #91067e, #cc1622, #c50091);
  background-size: 270% 100%;
  width: 27%;
  margin: 0px 36.5%;
  aspect-ratio: 4.9/1;
  font-size: 1.3vw;
  font-family: Parastoo-Bold;
  border: none;
  border-radius: 25px;
  box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.874);
  transition: all 500ms ease-in-out;
}
.fbtn:hover {
  cursor: pointer;
  box-shadow: -1px 1px 10px rgba(0, 0, 0, 1);
  background-position-x: 100%;
}
#lDiv {
  z-index: 1;
  height: 100%;
  width: 4%;
  background: linear-gradient(to top, white , #90b9c8);
  position: absolute;
  bottom: 0;
  left: 0;
  border-top-right-radius: 150px 100px;
  border-bottom-right-radius: 20px;
  filter: drop-shadow(5px 5px 4px black) opacity(1) contrast(100%);
}
#rDiv {
  z-index: 1;
  height: 100%;
  width: 4%;
  background: linear-gradient(to top, white , #90b9c8);
  filter: drop-shadow(-1px 5px 4px black) opacity(1) contrast(100%);
  position: absolute;
  bottom: 0;
  right: 0;
  border-top-left-radius: 150px 100px;
  border-bottom-left-radius: 20px;
}
#ic {
    transform: scale(1.5);
    position: absolute;
    margin: 0px calc(50% - 58px);
    margin-top: -12%;
    background-image: linear-gradient(
      to left,
      #250b30,
      #411253,
      #840d61,
      #b81f6b,
      #d6517f,
      #d61674,
      #980c68,
      #660a46,
      #531253,
      #250b30
    );
    background-size: 1500% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    animation: flasher 70s infinite linear;
    filter: drop-shadow(1px 2px 2px black);
  }
  @keyframes flasher {
    from {
      background-position: 0%;
    }
    to {
      background-position: 1500%;
    }
  }
  .imgContainer{
    width: 35%;
    aspect-ratio: 3/2;
    margin: 0px auto;
    position: relative;
  }
  #img{
    position: absolute;
    width: 130%;
    right: -19%;
    top: -45%;
    filter: drop-shadow(-1px 2px 2px black) contrast(90%);
  }

  /* make it responsive */
@media (max-width : 1000px) {
  .contain{
    font-size: 1.9vw;
    width: 75%;
    margin:0 12.5%;
    margin-bottom: 10%;
  }
  .contain b{
    font-size: 2.1vw;
  }
  .imgContainer{
    width: 40%;
  }
}
@media (max-width : 880px) {
  #ic{
    margin-top: -15%;
    filter: drop-shadow(0px 2px 2px black);
  }
}
@media (max-width: 800px){
  .thisClass{
    margin-top: 15%;
  }
  #ic{
    margin-top: -20%;
  }
  .contain{
    font-size: 2.2vw;
  }
  .contain b{
    font-size: 2.4vw;
  }
  .fbtn{
    font-size: 1.9vw;
    width: 35%;
    margin:  0px 30%;
  }
  .imgContainer{
    width: 55%;
  }
}
@media (max-width : 680px){
  .contain{
    font-size: 2.6vw;
    width: 75%;
    margin: 10% 12.5%;
    margin-top: 0;
  }
  .contain b{
    font-size: 2.8vw;
  }
}
@media (max-width : 580px){
  #ic{
    transform: scale(1.2);
    margin-right: calc(50% - 54px);
  }
}
@media (max-width : 480px) {
  .contain{
    font-size: 2.8vw;
  }
  .contain b{
    font-size: 3vw;
  }
  .fbtn{
    color: white;
    width: 45%;
    margin: 0px 48%;
    font-size: 2.9vw;
  }
  .thisClass{
    margin-top: 25%;
  }
  #ic{
    margin-top: -30%;
  }
  .imgContainer{
    width: 70%;
  }
}
@media (max-width : 420px) {
  .contain{
    font-size: 3.2vw;
  }
  .contain b{
    font-size: 3.4vw;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  .contain h3{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  .fbtn{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
}